import React, { useEffect } from 'react';
import { useMutation } from '@apollo/client';
import Jumbotron from '../components/Jumbotron';
import { ADD_ORDER } from '../utils/mutations';
import { idbPromise } from '../utils/helpers';

function Success() {
  const [addOrder] = useMutation(ADD_ORDER);

  useEffect(() => {
    async function saveOrder() {
      // use idbPromise() to get all of the cart items
      const cart = await idbPromise('cart', 'get');
      //map cart items into an array of product IDs
      const products = cart.map(item => item._id);

      // once we have product ids we can pass them to addOrder() mutation, then we can delete all of the ids from the indexedDB store
      if (products.length) {
        const { data } = await addOrder({ variables: { products } });
        const productData = data.addOrder.products;

        productData.forEach((item) => {
          idbPromise('cart', 'delete', item);
        })
      }
      // go back to homepage after 3 seconds
      setTimeout(() => {
        window.location.assign('/');
      }, 3000);
    }
    saveOrder();
    
  }, [addOrder])
  return(
    <div>
      <Jumbotron>
        <h1>Success!</h1>
        <h2>Thank you for your purchase!</h2>
        <h2>You will now be redirected to the homepage</h2>
      </Jumbotron>
    </div>
  );
};

export default Success;